<script>
// import { createToaster } from "@meforma/vue-toaster";
// const toaster = createToaster({ /* options */ });
import axios from 'axios';
export default 
{
    name:"TheEditor",
      data: function(){
    return {
      editor:[],
      setting:null,
       pageNumber:1,
    }
  },

  mounted () {
    this.getEditorData();
    
  },
  methods:{
    getEditorData(id){
        this.pageNumber = id ;

        axios.get(`editor_picks_news?page=${this.pageNumber}`)
        .then(res => {

            this.editor = res.data.data ;
            this.setting = res.data;
            this.pageNumber = res.data.meta.pagination.total_pages ;

        })
    }
  }
}
</script>